#messagesDisplay {
    /* The 3 lines below make sure the the right column of the chats does not exceed the limit of the entire grid, and if so- a vertical scroller is used. And we also make sure that NO horizontal scroller is shown. */
    overflow-y: auto;
    overflow-x: hidden;
    max-height: 80%;

    margin: 0;
    padding: 10px;
    width: 100%;
    min-height: 80%;
    clear: both;
    top: 50px;
    background-image: url("finalWhatsappBackground.png");
    background-position: center;
    background-size: cover;
    /* background-color: #fdf6ed; */
    border-top-style: solid;
    border-left-style: solid;
    border-width: 1px;
    border-color: lightgray;
}

.row .messages {
    padding: 0;
    border-top-style: solid;
    border-width: 1px;
    border-color: lightgray;
}

#rightChatsColumn {
    max-height: 99%;
}

#welcomeMessage {
    font-size: 20px;
    color: #838383;
}