.chats h6 {
    padding-left: 35px;
    padding-top: 5px;
}


.chats .list-group-item {
    padding-left: 10px;
    border-bottom-style: solid;
    border-top-style: solid;
    border-width: 1px;
    border-color: lightgray;
    height: 65px;
    font-size: 13px;
}

.smallMessages {
    /* The three lines below make sure that only one line of the last message in the chat is displayed */
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    padding-left: 35px;
    position: relative;
    bottom: 15px;
}

.smallProfilePic {
    margin-top: 10px;
    display: block;
    width: 30px;
    height: 30px;
    overflow: hidden;
    border-radius: 50%;
    float: left;
}