#chatsWindow {
    margin: 0 auto;
    background-color: #f8f8f8;
    border-color: #eeeeee;
    border-style: solid;
    border-width: 1px;
    border-radius: 10px 10px 0 0;
    width: 65%;
    height: 100%;
}

#contactList {
    /* The 3 lines below make sure the the right column of the chats does not exceed the limit of the entire grid, and if so- a vertical scroller is used. And we also make sure that NO horizontal scroller is shown. */
    overflow-y: auto;
    overflow-x: hidden;
    height: 87%;
}

.row .chats {
    padding: 0;
    border-top-style: solid;
    border-width: 1px;
    border-color: lightgray;
    height:100%
}

#chatsWindow .list-group {
    clear: both;
    --bs-list-group-border-color: none;
    --bs-list-group-action-hover-bg: #daeaff;
    --bs-list-group-action-active-bg: #bedaff;
    --bs-list-group-border-radius: 0;
}

#noContactsMessage {
    color: darkgray;
    font-size: 20px;
    white-space: pre-wrap;
}